@keyframes atPromptFadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes atPromptTypingCursorBlink {
    0%,
    50% {
        opacity: 1;
    }
    51%,
    100% {
        opacity: 0;
    }
}

.at-prompt-fade-in {
    animation: atPromptFadeIn 0.6s ease-out;
}
